<script>
import { useStore } from 'vuex'

export default{
  props: ['product'],
  setup(){
    const store = useStore()
    const add = product => {
      store.dispatch('addToCart', product)
    }
    const addList = product => {
      store.dispatch('addToList', product)
    }

    return { add, addList }

  }
}
</script>


